.header {
  box-sizing: border-box;
      display:flex;
      /* position: fixed; */
      align-items: center;
      
  @media screen and (min-width: 1280px) {
    min-width: 1280px;
    margin: 0 auto;
    padding: 20px 16px 20px 16px;
  }

  @media screen and (width: 768px) {
    min-width: 768px;
    max-width: 1280px;
    composes: tabletContainer from "../../global.css";
    justify-content: space-between;
  }

  @media screen and (max-width: 767px) {
    justify-content: space-between;
    max-width: 767px;
    width: auto;
    min-width: 320px;
  }
 
